import axios from "axios";
import { wrapper } from "../modules/login";
import { emailGroup, email, password, result, middleInputsHolder, loginButton, loader } from "../modules/login";

// Function to set the cookie with an expiration time of 4 hours

const setCookie = (name, value, hours) => {
  let expires = "";
  if (hours) {
    const date = new Date();
    date.setTime(date.getTime() + (hours * 60 * 60 * 1000)); // Convert hours to milliseconds
    expires = "; expires=" + date.toUTCString();
  }
  document.cookie = name + "=" + (value || "") + expires + "; path=/";
};

export const handleLogin = () => {
  loginButton.disabled = true;
  loginButton.innerText = "";
  loginButton.appendChild(loader);
  axios.post('https://api.cashly.me/login/', {
      username: email.value,
      password: password.value,
    })
    .then(response => {
      const data = response.data;
      if (data.response.code === 0) {
        result.innerText = data.response.payload.message;
        result.style.color = '#ff8383';
        result.style.fontSize = 'large';
        middleInputsHolder.insertBefore(result, emailGroup);
        setTimeout(() => {
          result.innerText = "";
          result.style.color = '';
          middleInputsHolder.removeChild(result);
        }, 5000);
      } else {
        const jwt = data.response.payload.resource;
        result.innerText = data.response.payload.message;
        result.style.color = '';
 // Save the token to a cookie with a 4-hour expiry
 setCookie('token', jwt, 4);
//  createOtpForm(wrapper);
///window.location.href = `/dashboard?token=${encodeURIComponent(jwt)}&email=${encodeURIComponent(email.value)}`; 
        window.location.href = `/otp?token=${encodeURIComponent(jwt)}&email=${encodeURIComponent(email.value)}`;
      }
    })
    .catch(error => {
      console.error('There was a problem with the login operation:', error);
    })
    .finally(() => {
      loginButton.disabled = false;
      loginButton.innerHTML = 'Login';
    });
};
